import React, {useState} from "react";

export const MainPills = ({content, initialActive, reloadContent}) => {
    let [active, setActive] = useState(initialActive)

    function makeTabs() {
        return Object.keys(content).map(key => {
            return (
                <li className={`nav-item bg-light`} key={key} onClick={() => {setActive(key)}}>
                    <div className={`nav-link ${active === key ? "active" : ""} text-dark px-3`} style={{cursor: "pointer"}}>
                        {key}
                    </div>
                </li>
            )
        })
    }

    function makeTabChildren() {
        // reload content
        if (reloadContent) {
            return content[active]
        }

        // keep everything loaded
        return (
            <div className="tab-content">
                {Object.keys(content).map(key => {
                    return (
                        <div className={`tab-pane ${active === key ? "active" : ""}`} key={key}>
                            { content[key] }
                        </div>
                    )
                })}
            </div>
        )
    }

    return (
        <div>
            <nav className="navbar navbar-light bg-light pb-0 px-0 mb-3">
                <div className={"container"}>
                    <ul className="nav nav-tabs">
                        {makeTabs()}
                    </ul>
                </div>
            </nav>
            <div className={"container"}>

                    { makeTabChildren() }

            </div>
        </div>
    )
}