import React from "react"
import {urlPrefix} from "./settings"
import {DetailView, listItemsLastReading} from "./DetailView";
import {SensorFrom} from "./SensorForms";


export class Sensor extends DetailView {
    constructor(props) {
        super(props);

        this.itemLabel = "Sensor"
        this.fetchUrlPath = "/sensors"
    }


    renderFormView() {
        return <SensorFrom handleClose={this.hideModal}
                  editMode={true}
                  initialState={{
                      ...this.item(),
                      interval: this.item().interval / 60,
                      retain_for: this.item().retain_for / 24 / 60 / 60,
                      url: decodeURIComponent(this.item().url)}}
        />
    }

    renderSubclassed() {
        return (
        <div>
            { this.item().type === "HTML" ? (
                <button
                    className={"btn btn-sm btn-outline-primary"}
                    onClick={() => {
                        let wnd = window.open(this.item().url)
                        wnd.document.write(this.item().last_reading.content)
                    }}>
                    <span className={"fa fa-external-link-alt mr-2"}/>
                    Open HTML content of last reading in new window
                </button>
            ) : null
            }
            <hr/>
            <h5>
                Channels
                <span className={"text-muted font-weight-normal"}> (last reading)</span>
            </h5>
            <ul className={"list-group"}>
                {listItemsLastReading(this.item().last_reading)}
            </ul>
        </div>
        )
    }


}