import React from "react"
import {urlPrefix} from "./settings"
import {ListItemCollapse, Modal} from "./Basics";
import moment from "moment";

function truncate(str) {
    if (str !== null) {
        str = str.toString()
        return str.length < 30 ? str : `${str.slice(0, 27)}...`
    }
}

let knownConverters = {
    elapsed: x => {
        return `${Math.round(x * 1000)} ms`
    },
    time: x => {
        return new Date(x * 1000).toLocaleString()
    },
    percentage: x => {
        return `${Math.round(x * 10) / 10} %`
    },
    seconds: x => {
        return `${Math.round(x)} (${Math.round(10 * x / 60 / 60 / 24) / 10} days)`
    },
    url: x => {
        return (
            <a href={decodeURIComponent(x)} target="_blank" rel="noopener noreferrer">
                { decodeURIComponent(x) }
            </a>
        )
    },
    interval: x => {
        return `${Math.round(x / 60 * 10) / 10} minutes`
    },
    retain_for: x => {
        return `${Math.round(x / 24 / 60 / 60 * 10) / 10} days`
    },
    expression: x => {
        return <div className={"text-monospace"}>{x}</div>
    },
    broken: x => {
        return !x ? <span className={"fas fa-check text-success"}/> : <span className={"fas fa-times text-danger"}/>
    },
    last_notify: x => {
        return x < 0 ?
            "never" :
            `${new Date(x * 1000).toLocaleString()} (${moment(x * 1000).fromNow()})`
    },
    queued_messages: x => {
        return x.map((item, idx) => {
            return <div key={idx}>
                {item}
            </div>
        })
    },
}
let converters = new Proxy(knownConverters, {
    get: (target, name) =>
        name in target ? target[name] : truncate
})


let blacklist = ["last_reading", "last_update", "kwargs", "enabled", "id", "name"]

let knownLabelConversions = {
    "url": "Target URL",
    "retain_for": "Retain readings for",
    "broken": "Working?",
    "last_notify": "Last notified",
    "queued_messages": "Queued messages",
}
let labelConversion = new Proxy(knownLabelConversions, {
    get: (target, name) =>
        name in target ? target[name] : <span className={"text-capitalize"}>{name}</span>
})


export function listItemsLastReading(lastReading) {
    if (lastReading == null) return null

    let listItems = Object.keys(lastReading).map(function (key) {
        return <ListItemCollapse
            key={key}
            label={key}
            value={converters[key](lastReading[key])}
        >
            {lastReading[key]}
        </ListItemCollapse>
    });
    return (
        listItems
    )
}


function AttributeRow({label, children}) {
    return (
        <div className={"row mb-2"}>
            <div className={"col-sm-4"}>
                <h5>
                    { label }
                </h5>
            </div>
            <div className={"col-sm-8"} style={{ overflow: "hidden" }}>
                { children }
            </div>
        </div>
    )
}


export class DetailView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {updating: false, showModal: false}

        // override these attributes
        this.itemLabel = "Item"
        this.fetchUrlPath = "/item"
    }

    // override me
    renderFormView() {
        return null
    }

    //override me
    renderSubclassed() {
        return null
    }


    refresh() {
        let url = urlPrefix + `${this.fetchUrlPath}/${this.props.id}/update`
        this.setState({ updating: true })
        fetch(url)
            .then(res => {return res.json()})
            .then(data => {
                this.setState({
                    item: {
                        ...this.item(),
                        last_reading: data
                    },
                    updating: false
                });
            })
    }

    item() {
        let out = this.props.globalState[this.itemLabel].find(obj => {
            return obj.id === this.props.id
        })
        return out == null ? {} : out
    }

    setItem(newItem) {
        let items = this.props.globalState[this.itemLabel]
        let idx = items.findIndex(obj => obj.id === newItem.id)
        items[idx] = newItem
        // set new items list
        let newState = {}
        newState[this.itemLabel] = items
        this.props.setGlobalState(newState)
    };

    update() {
        let url = urlPrefix + `${this.fetchUrlPath}/${this.props.id}`
        fetch(url)
            .then(res => {return res.json()})
            .then(data => {
                this.setItem(data)
            })
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.id !== prevProps.id) {
            if (this.item().last_reading == null) {
                this.update()
            }
        }
    }

    delete() {
        let url = urlPrefix + `${this.fetchUrlPath}/${this.props.id}`;
        fetch(url, {
            method: 'DELETE',
            data: this.item()
        });
    }

    hideModal = () => {
        this.setState({ showModal: false });
    };

    attributeRows(attributes) {
        let statefulConverters = {
            variables: x => {
                return Object.keys(x).map(key => {
                    let obj = x[key]
                    let sensorName = obj.id
                    try {
                        sensorName =  this.props.globalState["Sensor"].find(s => s.id === obj.id).name
                    } catch (e) {}

                    return <div className={"text-monospace text-large"} key={key}>
                        <code className={""}>{key}</code> = <kbd>
                            {sensorName}
                        </kbd>.
                        <kbd>{obj.channel}</kbd>
                        [<kbd>{obj.row}</kbd>]
                    </div>
                })
            }
        }

        if (attributes == null) return
        return Object.entries(attributes).map(entry => {
            let key = entry[0]
            let value = entry[1]
            return (value != null && value !== "" && !(blacklist.includes(key))) ? (
                <AttributeRow label={labelConversion[key]} key={key}>
                    { key in statefulConverters ? statefulConverters[key](value) : converters[key](value)}
                </AttributeRow>
            ) : null

        })
    }



    render() {
        // { this.state.name }
        return (
            <div className={"card shadow"}>
                { this.state.showModal ? (
                    <Modal title={"Edit item"}
                           show={true}
                           handleClose={this.hideModal}>
                        { this.renderFormView() }
                    </Modal>
                ) : null}

                <div className={"card-header bg-dark py-3"}>
                    <h2 className={"mb-0 text-white"}>
                        {this.item().name || ""}
                        <span className={"float-right"}>
                            <button
                                className={"btn btn-light mr-2"}
                                onClick={() => this.refresh()}
                            >
                                {this.state.updating ?
                                    <i className={"spinner-border spinner-border-sm"}/> :
                                    <i className={"fa fa-sync"}/>}
                            </button>
                            <button
                                className={"btn btn-light mr-2"}
                                onClick={() => this.setState({showModal: true})}
                            >
                                <i className={"fa fa-edit"}/>
                            </button>
                            <button
                                className={"btn btn-light"}
                                onClick={() => this.delete()}
                            >
                                <i className={"fa fa-trash text-danger"}/>
                            </button>
                        </span>
                    </h2>
                </div>
                <div className={"card-body"}>
                    { this.attributeRows(this.item()) }
                    { this.renderSubclassed() }
                </div>
            </div>
        )
    }
}