import React from 'react';
import {TriggerFrom} from "./TriggerForms";
import {ListDetailView} from "./ListDetailView";
import {Trigger} from "./Trigger";


export class TriggerView extends ListDetailView {
    constructor(props) {
        super(props);

        this.itemLabel = "Trigger"
        this.basicEventHandlerItemPrefix = "trigger"
        this.fetchUrlPath = "/triggers"
    }

    renderDetailView() {
        return <Trigger id={ this.state.active }
                        eventManager={this.props.eventManager}
                        globalState={this.props.globalState}
                        setGlobalState={this.props.setGlobalState}
        />
    }

    renderFormView() {
        return <TriggerFrom handleClose={this.hideModal}/>
    }
}



