import React, {useState} from 'react';
import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.css';
import './custom.css'
//import './bootstrap-dark/bootstrap-dark.scss'
import { SensorView } from './SensorView.js';
import { eventManager } from "./events";
import { MainPills } from "./AppView";
import {TriggerView} from "./TriggerView";
import {ActionView} from "./ActionView";


// create state
let events = new eventManager()

function App() {
    let [state, _setState] = useState({})

    function setState(newState) {
        _setState({...state, ...newState})
    }

    return (
        <MainPills
            initialActive={"Sensors"}
            content={{
                "Sensors" : <SensorView eventManager={events} globalState={state} setGlobalState={setState}/>,
                "Triggers" : <TriggerView eventManager={events} globalState={state} setGlobalState={setState}/>,
                "Actions" : <ActionView eventManager={events} globalState={state} setGlobalState={setState}/>,
            }}
        />
    )
}


ReactDOM.render(
  <React.StrictMode>
          <App/>
  </React.StrictMode>,
  document.getElementById('root')
);
