import React, {useState} from 'react';
import {urlPrefix} from "./settings";
import {FromGroup, FromGroupSelect} from "./Basics";


export function SensorFrom(props) {
    const initialState = {"type": "HTML", "interval": "5", "name": "", "url": "", "retain_for": "90",
        ...props.initialState};
    let [state, setState] = useState(initialState)

    let editMode = "editMode" in props ? props.editMode : false

    function handleSubmit(event) {
        event.preventDefault()

        let data = {
            ...state,
            "interval": state.interval * 60,
            "retain_for": state.retain_for * 24 * 60 * 60,
        }

        if (editMode) {
            // edit sensor
            fetch(urlPrefix + "/sensors/" + state.id, {
                method: "put",
                body: JSON.stringify(data)
            })
        } else {
            // new sensor
            fetch(urlPrefix + "/sensors", {
                method: "post",
                body: JSON.stringify(data)
            })
                .then(res => {
                    setState(initialState)
                })
        }

        try {
            props.handleClose()
        } catch (e) {}
    }

    function handleSetState(event) {
        let newState = {};
        newState[event.nativeEvent.target.name] = event.nativeEvent.target.value
        setState({
            ...state,
            ...newState
        })
    }

    return (
        <form onSubmit={handleSubmit} className={"row"}>
            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Type</h4>
                <FromGroupSelect
                    label={" "}
                    type={"select"}
                    value={state.type}
                    list={"form-add-sensor-types"}
                    name={"type"}
                    onChange={handleSetState}
                    disabled={editMode}
                >
                    <option value={"HTML"}>HTML - Grabs HTTP response from a URL</option>
                    <option value={"CPUPercentage"}>CPUPercentage - Host system CPU usage</option>
                    <option value={"RAMPercentage"}>RAMPercentage - Host system memory usage</option>
                    <option value={"Uptime"}>Uptime - Host system uptime</option>
                </FromGroupSelect>
            </div>

            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Info</h4>
                <FromGroup label={"Name:"}
                           name={"name"}
                           value={state.name}
                           onChange={handleSetState}/>
                <FromGroup label={"Update interval:"}
                           type={"number"}
                           name={"interval"}
                           placeholder={"5"}
                           suffix={"minutes"}
                           value={state.interval}
                           onChange={handleSetState}/>
                <FromGroup label={"Retain for:"}
                           type={"number"}
                           name={"retain_for"}
                           placeholder={"90"}
                           suffix={"days"}
                           value={state.retain_for}
                           onChange={handleSetState}/>
            </div>

            <div className={"col-lg-4"}>
                <h4>Type Specific Info</h4>
                { state.type === "HTML" ? (
                    <FromGroup label={"Target URL:"}
                               type={"text"}
                               name={"url"}
                               value={state.url}
                               onChange={handleSetState}/>
                ) : "" }
            </div>

            <div className={"col-lg-12"}>
                <button type={"submit"} className={"btn btn-primary mt-2 px-4"}>
                    {editMode ? "Apply" : "Create Sensor"}
                </button>
            </div>
        </form>
    )
}


