import React from 'react';
import {ActionForm} from "./ActionForms";
import {ListDetailView} from "./ListDetailView";
import {Action} from "./Action";


export class ActionView extends ListDetailView {
    constructor(props) {
        super(props);

        this.itemLabel = "Action"
        this.basicEventHandlerItemPrefix = "action"
        this.fetchUrlPath = "/actions"
    }

    renderDetailView() {
        return <Action id={ this.state.active }
                       eventManager={this.props.eventManager}
                       globalState={this.props.globalState}
                       setGlobalState={this.props.setGlobalState}
        />
    }

    renderFormView() {
        return <ActionForm handleClose={this.hideModal}/>
    }
}



