import React from "react"
import {DetailView, listItemsLastReading} from "./DetailView";
import {TriggerFrom} from "./TriggerForms";


export class Trigger extends DetailView {
    constructor(props) {
        super(props);

        this.itemLabel = "Trigger"
        this.fetchUrlPath = "/triggers"
    }

    renderFormView() {
        return <TriggerFrom
            handleClose={this.hideModal}
            editMode={true}
            initialState={this.item()}
        />
    }

    renderSubclassed() {
        return (
            <div>
                <hr/>
                <h5>
                    Last Check
                </h5>
                <ul className={"list-group"}>
                    {listItemsLastReading(this.item().last_reading)}
                </ul>
            </div>
        )
    }


}