import React from "react"
import {DetailView, listItemsLastReading} from "./DetailView";
import {ActionForm} from "./ActionForms";


export class Action extends DetailView {
    constructor(props) {
        super(props);

        this.itemLabel = "Action"
        this.fetchUrlPath = "/actions"
    }

    renderFormView() {
        return <ActionForm
            handleClose={this.hideModal}
            editMode={true}
            initialState={this.item()}
        />
    }

    renderSubclassed() {
        return (
            <div>
                <hr/>
                <h5>
                    Last Message
                </h5>
                <ul className={"list-group"}>
                    {listItemsLastReading(this.item().last_reading)}
                </ul>
            </div>
        )
    }


}