import React, {useEffect, useState} from 'react';
import {urlPrefix} from "./settings";

let daysToSeconds = 24 * 60 * 60

function AddRowButton({onClick}) {
    return (
        <div className={"row mt-1"}>
            <div className={"pl-2"} style={{width: "48px"}}>
                <button type="button" className={"btn btn-sm btn-light mr-2"} onClick={(e) => {
                    e.preventDefault()
                    if (onClick == null) return
                    onClick()
                }}>
                    <span className={"fa fa-plus"} />
                </button>
            </div>
        </div>
    )
}


function InputRow({handleRemove, value, handleChange}) {
    let [state, setState] = useState(value)
    let [sensorOptions, setSensorOptions] = useState([])
    let [numberUpdating, setNumberUpdating] = useState(0)
    let [lastReading, setLastReading] = useState({time: 0})

    function updateSensorList() {
        let url = urlPrefix + `/sensors/`
        fetch(url)
            .then(res => {return res.json()})
            .then(res => {
                setSensorOptions(res)
            })
    }

    function incrementNumberUpdating(inc=1) {
        setNumberUpdating(n => {
            return n + inc
        });
    }

    function updateChannelList(id, row=state.row) {
        let url = `${urlPrefix}/query/${id}?row=${row}`
        incrementNumberUpdating()
        fetch(url)
            .then(res => {return res.json()})
            .then(res => {
                if (res[0] != null) setLastReading(res[0])
                incrementNumberUpdating(-1)
            })
    }

    function onClickRemove(e) {
        e.preventDefault()
        handleRemove()
    }

    function onClickRefresh(e) {
        e.preventDefault()
        updateChannelList(state.id)
    }

    function onChange(event) {
        event.preventDefault()
        let newState = {...state};
        newState[event.nativeEvent.target.name] = event.nativeEvent.target.value
        setState(newState)
        handleChange(newState)
        if (event.nativeEvent.target.name === "id") {
            updateChannelList(event.nativeEvent.target.value)
        }
        if (event.nativeEvent.target.name === "row") {
            updateChannelList(state.id, event.nativeEvent.target.value)
        }
    }

    useEffect(() => {
        setState(value);
        updateSensorList()
        if (value.id) updateChannelList(value.id)
    }, [value.id]);

    return (
        <div className={"row py-2"}>
            <div className={"pl-2"} style={{width: "36px"}}>
                <button type="button" className={"btn btn-sm btn-light mr-2"} onClick={onClickRemove}>
                    <span className={"fa fa-minus"} />
                </button>
            </div>
            <div className={"pl-2"} style={{width: "35%"}}>
                <select
                       className="form-control form-control-sm mr-2"
                       placeholder="Sensor"
                       name={"id"}
                       value={ state.id }
                       onChange={onChange}>
                    <option value=""/>
                    { sensorOptions.map(item => {
                        return <option key={item.id} value={item.id}>{item.name}</option>
                    }) }
                </select>
            </div>
            <div className={"pl-2"} style={{width: "35%"}}>
                <select
                       className="form-control form-control-sm mr-2"
                       placeholder="Channel"
                       name={"channel"}
                       value={ state.channel }
                       onChange={onChange}>
                    { Object.keys(lastReading).map(item => {
                        return <option key={item} value={item}>{item}</option>
                    }) }
                </select>
            </div>
            <div className={"pl-2"} style={{width: "20%", maxWidth: "90px"}}>
                <input type="number"
                       className="form-control form-control-sm mr-2"
                       placeholder="-1"
                       name={"row"}
                       value={ state.row }
                       onChange={onChange}/>
            </div>

            <div className={"col-12 mt-1"} />

            <div className={"pl-2"} style={{width: "36px"}}> </div>
            <div className={"pl-2"} style={{width: "70%"}}>
                <div className="input-group input-group-sm">
                    <input type="text"
                           className="form-control"
                           placeholder="Variable name (can be used in Expression)"
                           name={"variable"}
                           value={ state.variable }
                           onChange={onChange}/>
                    <div className="input-group-append w-50">
                        <div className="input-group-text w-100 bg-light">
                            <span className={"text-truncate text-nowrap text-monospace"}>
                                = { lastReading[state.channel] }
                            </span>
                        </div>
                    </div>
                </div>
            </div>
            <div className={"pl-2"} style={{width: "20%", maxWidth: "90px"}}>
                <button type="button" className={"btn btn-sm btn-light w-100"} onClick={onClickRefresh}>
                    {numberUpdating > 0 ?
                        <i className={"spinner-border spinner-border-sm"}/> :
                        <i className={"fa fa-sync"}/>}
                </button>
            </div>

        </div>
    )
}

const emptyInputRow = {channel: "time", row: -1, id:"", variable: "t"}
const defaultInitialState = {
    name: "New Trigger",
    retain_for: 90 * daysToSeconds,
    expression: "",
    variables: {"t": emptyInputRow},
    action_ids: [],
    message: "",
}

export function TriggerFrom({editMode=false, handleClose, initialState=defaultInitialState}) {
    let {variables, ...initial} = initialState
    let [state, setState] = useState(initial)

    let initialInputRows = Object.keys(variables).map(key => {
        return {
            ...variables[key],
            variable: key,
        }
    })
    let [inputRows, setInputRows] = useState(initialInputRows)

    let [expressionMessage, setExpressionMessage] = useState("")
    let [expressionError, setExpressionError] = useState(false)

    // evaluate expression when form is shown
    useEffect(() => {
        if (initialState.expression === "") return
        evaluateExpression(initialState.expression, getVariables())
    }, [initialState.expression]);

    function reset() {
        setExpressionMessage("")
        setExpressionError("")
        setInputRows(initialInputRows)
        setState(initial)
    }

    function handleSubmit(event) {
        event.preventDefault()

        let data = {
            ...state,
            variables: getVariables(),
        }

        if (editMode) {
            // edit trigger
            fetch(urlPrefix + "/triggers/" + state.id, {
                method: "put",
                body: JSON.stringify(data)
            })
        } else {
            // new trigger
            fetch(urlPrefix + "/triggers", {
                method: "post",
                body: JSON.stringify(data)
            })
                .then(() => {
                    reset()
                })
        }

        try {
            handleClose()
        } catch (e) {}
    }

    function onChange(e) {
        e.preventDefault()
        let target = e.nativeEvent.target
        let newState = {...state}
        let value = target.value
        if (target.dataset.factor && value != null) {
            value *= target.dataset.factor
        }
        newState[target.name] = value
        setState(newState)
    }

    function getVariables() {
        let variables = {}
        inputRows.map(row => {
            let {variable, ...value} = row
            variables[variable] = value
            return null
        })
        return variables
    }

    function evaluateExpression(expression, variables) {
        let data = {
            expression: expression,
            variables: variables,
        }

        fetch(urlPrefix + "/evaluate", {
            method: "post",
            body: JSON.stringify(data)
        })
            .then(res => {return res.json()})
            .then(res => {
                setExpressionMessage(res.message)
                setExpressionError(res.error)
            })
    }

    return (
        <form onSubmit={handleSubmit}>
            <div className={"row"}>
                <div className={"col-md-6"}>
                    <label>Name:</label>
                    <input type="text"
                           className="form-control mb-2"
                           name={"name"}
                           value={ state.name }
                           onChange={onChange}/>
                </div>
                <div className={"col-md-6"}>
                    <label>Retain history for:</label>
                    <div className="input-group mb-2">
                        <input type="number"
                               className="form-control"
                               name={"retain_for"}
                               data-factor={daysToSeconds}
                               value={ state.retain_for / daysToSeconds }
                               onChange={onChange}/>
                        <div className="input-group-append">
                            <span className="input-group-text">days</span>
                        </div>
                    </div>
                </div>
            </div>

            <hr/>

            <h4>Variables</h4>
            <div className={"row"}>
                <div style={{width: "36px"}} className={"px-2"}> </div>
                <div style={{width: "35%"}} className={"px-2"}>Sensor:</div>
                <div style={{width: "35%"}} className={"px-2"}>Channel:</div>
                <div style={{width: "20%", maxWidth: "90px"}} className={"px-2"}>Index:</div>
            </div>
            { inputRows.map((row, idx) => {
                return <InputRow
                    key={idx}
                    value={inputRows[idx]}
                    handleRemove={() => {
                        // remove the current row
                        setInputRows(
                            inputRows.filter((value, index) => { return index !== idx;})
                        )
                    }}
                    handleChange={(newState) => {
                        let newRows = [...inputRows]
                        newRows[idx] = newState
                        setInputRows(newRows)
                    }}
                />
            }) }
            <AddRowButton onClick={() => {
                setInputRows([
                    ...inputRows,
                    inputRows.length > 0 ? inputRows.slice(-1)[0]  : emptyInputRow
                ])
            }} />


            <hr/>
            <h4>Expression</h4>

            <input type="text"
                   className="form-control text-monospace mb-1"
                   placeholder={"A == 3 or \"substing\" in long_string"}
                   onChange={e => {
                       e.preventDefault()
                       setState({
                           ...state,
                           expression: e.nativeEvent.target.value
                       })
                       evaluateExpression(e.nativeEvent.target.value, getVariables())
                   }}
                   value={state.expression}
            />

            {state.expression === "" ? null :
                <div className={`alert ${expressionError ? "alert-danger" : 
                    [true, false].includes(expressionMessage) ? "alert-success" : "alert-warning"}`}>
                    {expressionMessage.toString()}
                </div>
            }

            <h4>Action</h4>
            <input type="text"
               className="form-control"
               placeholder={"asdad-asdasd-adsads-dasdsa-adsasd"}
               onChange={e => {
                   e.preventDefault()
                   setState({
                       ...state,
                       action_ids: [e.nativeEvent.target.value]
                   })
               }}
               value={state.action_ids[0]}
            />

            <input type="text"
               className="form-control"
               placeholder={"Message"}
               onChange={e => {
                   e.preventDefault()
                   setState({
                       ...state,
                       message: e.nativeEvent.target.value
                   })
               }}
               value={state.message}
            />

            <div className={"col-lg-12"}>
                <button type={"submit"} className={"btn btn-primary mt-2 px-4"}>
                    {editMode ? "Apply" : "Create Trigger"}
                </button>
            </div>
        </form>
    )
}


