import React from "react";
import {urlPrefix} from "./settings";
import {ListItemSelect, Modal} from "./Basics";

export class ListDetailView extends React.Component {
    constructor(props) {
        super(props);
        this.state = { active: "", showModal: false, filter: "" }

        // override these attributes
        this.itemLabel = "Item"
        this.basicEventHandlerItemPrefix = "item"
        this.fetchUrlPath = "/items"
    }

    // override me
    renderDetailView() {
        return null
    }

    // override me
    renderFormView() {
        return null
    }


    setItems(items) {
        let newState = {}
        newState[this.itemLabel] = items
        this.props.setGlobalState(newState)
    }

    items() {
        if (this.props.globalState[this.itemLabel] == null) {
            this.props.globalState[this.itemLabel] = []
        }
        return this.props.globalState[this.itemLabel]
    }

    updateItem(id) {
        let url = urlPrefix + `${this.fetchUrlPath}/${id}`
        fetch(url)
            .then(res => {return res.json()})
            .then(data => {
                let items = this.props.globalState[this.itemLabel]
                let idx = items.findIndex(obj => obj.id === id)
                items[idx] = data
                this.setItems(items)
            })
    }

    eventHandler(event) {
        if (event.message === this.basicEventHandlerItemPrefix + " deleted") {
            // remove item from list
            let id = event.data.id
            let newIdx = 0
            let filtered = this.items().filter((item, index) => {
                if (item.id === id) {
                    newIdx = index
                }
                return item.id !== id;
            });

            // delete item
            this.setItems(filtered)

            // focus on new item
            if (this.items().length > 0) {
                newIdx = this.items().length <= newIdx ? newIdx - 1 : newIdx
                this.setState( {
                    active: this.items()[newIdx].id
                } )
            }

        } if (event.message === this.basicEventHandlerItemPrefix + " added") {
            this.setItems([
                ...this.items(),
                event.data
            ] )
        } if (event.message === this.basicEventHandlerItemPrefix + " edited") {
            // update the relevant item
            this.updateItem(event.data.id)
        } if (event.message === this.basicEventHandlerItemPrefix + " updated") {
            let id = event.data.id
            if (this.state.active === id) {
                // update item if currently visible
                this.updateItem(id)
            } else {
                // remove outdated reading if not visible
                let items = this.items()
                let idx = items.findIndex(obj => obj.id === id)
                if (items[idx].last_reading) {
                    items[idx].last_reading = null
                }
                this.setItems(items)
            }
        }
    }

    update() {
        let url = urlPrefix + this.fetchUrlPath
        fetch(url)
            .then(res => {return res.json()})
            .then(res => {
                this.setItems(res)
                if (this.state.active === "" && this.items().length > 0) this.setState({
                    active: "id" in this.items()[0] ? this.items()[0].id : ""
                })
            })
    }

    componentDidMount() {
        this.update()
        if (this.props.eventManager) {
            this.props.eventManager.subscribe(this)
        }
    }

    componentWillUnmount() {
        if (this.props.eventManager) {
            this.props.eventManager.unsubscribe(this)
        }
    }

    ItemList(items) {
        if (items == null) return null

        return items.map(item => {
            return <ListItemSelect
                key={item.id}
                onClick={() => this.setState({ active: item.id })}
                active={this.state.active === item.id}
                iconRight={ <div className={"badge badge-light font-weight-normal"}>{ item.type }</div> }
            >
                { item.name }
            </ListItemSelect>
        });
    }

    hideModal = () => {
        this.setState({ showModal: false });
    };

    render() {
        return (
            <div className={"row"}>
                <div className={"col-md-4 mb-3"}>
                    <Modal title={`Add New ${this.itemLabel}`}
                           show={this.state.showModal}
                           handleClose={this.hideModal}>
                        {this.renderFormView()}
                    </Modal>
                    <ul className={"list-group shadow"}>
                        <li className={"bg-light list-group-item btn btn-outline-primary text-dark py-3"}
                            style={{
                                cursor: "pointer",
                                borderBottomLeftRadius: 0,
                                borderBottomRightRadius: 0,
                                 height: "76px" }}
                            onClick={() => this.setState({ showModal: true })}
                        >
                            <span className={"h2 align-bottom float-left mb-0"}>
                                New {this.itemLabel}
                            </span>
                            <h2 className={"mb-0"}>
                                <span className={"float-right"}>
                                    <i className={"fa fa-xs fa-plus"}/>
                                </span>
                            </h2>

                        </li>
                        <input className={"bg-light input-group-sm list-group-item icon-placeholder py-2"}
                               placeholder="&#xf002; Filter"
                               value={this.state.filter}
                               onChange={event => this.setState({filter: event.target.value})}
                        />
                        { this.ItemList( this.items().filter(item => {
                            let filter = this.state.filter.toLowerCase()
                            return Object.keys(item).some(key => {
                                try {
                                    return item[key].toLowerCase().includes(filter)
                                } catch (e) {
                                    return false
                                }
                            })
                            }))
                        }
                    </ul>
                </div>
                <div className={"col-md-8 mb-3"}>
                    {this.renderDetailView()}
                </div>
            </div>

        )
    }
}



