import React from 'react';
import {SensorFrom} from "./SensorForms";
import {Sensor} from "./Sensor";
import {ListDetailView} from "./ListDetailView";


export class SensorView extends ListDetailView {
    constructor(props) {
        super(props);

        this.itemLabel = "Sensor"
        this.basicEventHandlerItemPrefix = "sensor"
        this.fetchUrlPath = "/sensors"
    }

    renderDetailView() {
        return <Sensor id={ this.state.active }
                       eventManager={this.props.eventManager}
                       globalState={this.props.globalState}
                       setGlobalState={this.props.setGlobalState}
        />
    }

    renderFormView() {
        return <SensorFrom handleClose={this.hideModal} />
    }
}



