import React, {useState} from 'react';
import {urlPrefix} from "./settings";
import {FromGroup, FromGroupSelect} from "./Basics";


export function ActionForm(props) {
    const initialState = {
        "type": "DebugAction",
        "interval": "5",
        "name": "",
        "url": "",
        "retain_for": "90",
        ...props.initialState};
    let [state, setState] = useState(initialState)

    let editMode = "editMode" in props ? props.editMode : false

    function handleSubmit(event) {
        event.preventDefault()

        let data = {
            ...state,
            "interval": state.interval * 60,
            "retain_for": state.retain_for * 24 * 60 * 60,
        }

        if (editMode) {
            // edit action
            fetch(urlPrefix + "/actions/" + state.id, {
                method: "put",
                body: JSON.stringify(data)
            })
        } else {
            // new action
            fetch(urlPrefix + "/actions", {
                method: "post",
                body: JSON.stringify(data)
            })
                .then(() => {
                    setState(initialState)
                })
        }

        try {
            props.handleClose()
        } catch (e) {}
    }

    function handleSetState(event) {
        let newState = {};
        newState[event.nativeEvent.target.name] = event.nativeEvent.target.value
        setState({
            ...state,
            ...newState
        })
    }

    function testAction() {
        let message = `${state.name}: Test message :)`;
        fetch(urlPrefix + "/actions/" + state.id + "/update", {
            method: "post",
            body: JSON.stringify({
                message: message,
                force_send: true
            })
        })
    }

    function pushoverInputs() {
        return [
            <FromGroup label={"Api token:"}
                       type={"text"}
                       name={"api_token"}
                       value={state.api_token}
                       key={"api_token"}
                       onChange={handleSetState}/>,
            <FromGroup label={"User key:"}
                       type={"text"}
                       name={"user_key"}
                       value={state.user_key}
                       key={"user_key"}
                       onChange={handleSetState}/>,
            <FromGroup label={"Device:"}
                       type={"text"}
                       name={"device"}
                       value={state.device}
                       key={"device"}
                       onChange={handleSetState}/>,
        ]
    }

    return (
        <form onSubmit={handleSubmit} className={"row"}>
            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Type</h4>
                <FromGroupSelect
                    label={" "}
                    type={"select"}
                    value={state.type}
                    list={"form-add-sensor-types"}
                    name={"type"}
                    onChange={handleSetState}
                    disabled={editMode}
                >
                    <option value={"DebugAction"}>DebugAction - Debug print statement</option>
                    <option value={"PushoverAction"}>Pushover - Sends a push notification</option>
                </FromGroupSelect>
            </div>

            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Info</h4>
                <FromGroup label={"Name:"}
                           name={"name"}
                           value={state.name}
                           onChange={handleSetState}/>
                <FromGroup label={"Cooldown:"}
                           type={"cooldown"}
                           name={"interval"}
                           placeholder={"5"}
                           suffix={"minutes"}
                           value={state.interval}
                           onChange={handleSetState}/>
                <FromGroup label={"Retain for:"}
                           type={"number"}
                           name={"retain_for"}
                           placeholder={"90"}
                           suffix={"days"}
                           value={state.retain_for}
                           onChange={handleSetState}/>
            </div>

            <div className={"col-lg-4"}>
                <h4>Type Specific Info</h4>
                { state.type === "PushoverAction" ? (
                    pushoverInputs()
                ) : "" }
            </div>

            <div className={"col-lg-12"}>
                <button type={"submit"} className={"btn btn-primary mt-2 px-4"}>
                    {editMode ? "Apply" : "Create Action"}
                </button>
                <button type={"button"} className={"btn btn-secondary mt-2 px-4 ml-2"} onClick={testAction}>
                    Send Test Notification
                </button>
            </div>
        </form>
    )
}


